class SBSession {
  constructor() {
    this.headers = {};
    this.cookies = {};
    this.$session = undefined;
  }
  loggedIn() {
    return this.$session.exists() && this.$session.has("user");
  }
  login(user, password) {}
  userData() {
    return this.$session.get("user");
  }
  fakeLogin() {
    this.$session.start();
    this.$session.set("user", {
      id: 1001,
      name: "arily",
    });
    return true;
  }
  gotSession() {
    return Boolean(this.$session);
  }
  setSession(session) {
    this.$session = session;
    if (this.loggedIn() && this.$sb) this.$sb.set("sbloginstat", true);
  }
  setStorage(sbstore) {
    this.$sb = sbstore;
  }
  destroySession() {
    this.$session.destroy();
  }
}
const s = new SBSession();
const VueSBSessionPlugin = {};
VueSBSessionPlugin.install = function(Vue, options) {
  Vue.mixin({
    beforeCreate() {
      s.setStorage(this.$sb);
      if (!s.gotSession()) s.setSession(this.$session);
    },
    methods: {
      SBLoggedIn() {
        const state = this.$sb.get("sbloginstat");
        // const mystate = this.$sb.getMy("sbloginstat")
        // console.log({state,mystate})
        return state;
      },
      SBUserData() {
        if (s.loggedIn()) {
          return s.userData();
        } else return null;
      },
      SBLogin(user, password) {},
      fakeLogin() {
        console.log("fakeLogin");
        if (s.fakeLogin()) {
          this.$sb.set("sbloginstat", true);
          this.$router.push({ name: "landscape" });
        }
      },
      logOut() {
        if (["private", "logged-in"].includes(this.zone)) {
          const result = confirm("really? you gonna lose access to this page.");
          if (!result) return;
        }
        s.destroySession();
        this.$sb.set("sbloginstat", false);
      },
    },
  });
  // Vue.prototype.$ss = new SBSession(Vue)
};
export default VueSBSessionPlugin;
