<script>
import axios from "axios";
import SectionLayout from "@/components/sb-layouts/components/SectionLayout";
export default {
  components: {
    SectionLayout
  },
  data() {
    return {
      register: {
        name: undefined,
        email: undefined,
        password: undefined
      },
      validationResult: {
        name: undefined,
        email: undefined,
        password: undefined
      },
      rejectReason: {
        name: undefined,
        email: undefined,
        password: undefined
      },
      inDev: true
    };
  },
  watch: {
    async "register.name"(newVal) {
      if (!newVal) this.validationResult.name = undefined;
      const existUsers = await axios
        .get(`${this.$root.rippleApiBase}/users/lookup`, {
          params: {
            name: newVal
          }
        })
        .then(response => {
          if (response.data.users) return response.data.users;
          else return [];
        })
        .then(users => {
          console.log(users);
          return users;
        })
        .then(users => users.some(user => user.username == newVal));
      if (!existUsers) {
        this.validationResult.name = true;
        this.rejectReason.name = undefined;
        return;
      }
      this.validationResult.name = false;
      this.rejectReason.name = "username exists";
    },
    "register.email"(val) {
      this.validationResult.email = this.validEmail(val);
    },
    "register.password"(val) {
      if (val == "" && !val) this.validationResult.password = undefined;
      if (val) this.validationResult.password = true;
      else this.validationResult.password = false;
    }
  },
  methods: {
    validEmail(email) {
      var re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return re.test(email);
    },
    testSB() {
      console.log("test sb");
      this.$sb.set("sbuser", this.register);
