import Vuex from "vuex";
// const myFuckingStore = {};
const shit = {};
shit.install = function(Vue, options) {
  Vue.use(Vuex);
  const store = new Vuex.Store({
    state: {
      sb: {},
    },
    mutations: {
      save(state, { item, payload }) {
        // myFuckingStore[item] = payload;
        Vue.set(state.sb, item, payload);
      },
    },
    actions: {
      async update({ commit, state }, { item, fn }) {
        const payload = await fn(state.sb[item]);
        // myFuckingStore[item] = payload;
        return commit("save", { item, payload });
      },
    },
  });

  Vue.mixin({
    store,
  });
  // const reactive = (item) => store.state.sb[item];
  const reactiveRoot = () => store.state.sb;
  Vue.prototype.$sb = {
    // reactive,
    get(item, fromStore) {
      // reactive(item);
      return store.state.sb[item];
      // return myFuckingStore[item];
    },
    // getMy(item) {
    //   return myFuckingStore[item];
    // },
    set(item, newValue) {
      // myFuckingStore[item] = newValue;
      return store.commit("save", { item, payload: newValue });
    },
    cbSet(item, fn) {
      return store.dispatch("update", { item, fn });
    },
    debug() {
      return reactiveRoot();
      // return myFuckingStore;
    },
  };
};
export default shit;